// import showHotelBox from '../joinStart/showHotelBox.vue'
// import showFoodsBox from '../joinStart/showFoodsBox.vue'
import showPosterBox from '../../components/sharePoster.vue'
import loadErr from '@/components/loadErr.vue'
import { mapGetters , mapActions } from 'vuex';

export default {
	data() {
		return {
			navColor: 'rgba(255,255,255,0)',//导航背景色
			navTitle: 0, //导航标题
			isShowNavBorder: false, //是否显示border
			// isShowHotelBox: false, //酒店列表
			// isShowFoodsBox: false, //餐饮列表
			isShowPorsterBox: false, //分享海报
			isShowOthInit: false, //查看联合发起人
			userInfo: {}, //用户信息
			
			isLoadErr: false,
			dataObj: {},
			lineObj: {}, //景点对象
			swiperArr: [], //swiper数组
			dear_id: null, //宗亲id
			curIdx: 0, //当前swiper idx
			unionArr: [], //联合发起人
			viewId: '', //跳转到某天
			
			// 考察相册
			albumIdx: 0, //相册当前下标
			albumImgs: [],//相册数组
			videoTop: null, //视频top值
			isPlayVideo: false, //是否自动播放视频
			
			isShowRouteList: false, //
			optRouteTxt: '展开', //
			
			isShowNameList: false, //默认收起
			optApplyTxt: '展开',
			
			isShowFeeBox: false, //费用
			isShowSmallFee: false, //默认不展示看消费
			
			isAuth: false, //登录状态
			isShareStatus: false,
			isFromStartName: false, //是否来自添加发起人
		}
	},
	components: { showPosterBox, loadErr },
	onLoad(opt) {
		 const scene = decodeURIComponent(opt.scene)
		 if(scene != 'undefined') {
			 this.dear_id = scene
		 }else {
			this.dear_id = opt.dear_id
		 }
		 if(opt.from  && opt.from == 'startName') {
		 	this.isFromStartName = true
		 }
		this.isShareStatus = this.$isSharePage
		
		if(!this.$userInfo) { //加载登录配置
			this.$api.userLogin((status) => {
				if(status == 1) {
					this.isAuth = true
					this.userInfo = this.$userInfo
				}
			}, this.dear_id)
		}else {
			this.isAuth = true
			this.userInfo = this.$userInfo
		}
		
		this.getAlbumArr()
	},
	onShow() {
		setTimeout(() => {
			this.getData()
		}, 10)
	},
	onHide() {
		this.isShowFeeBox = false //关闭费用提示框
	},
	methods: {
		...mapActions([
			'toGetActInfo',
			'toSaveSpotInfo'
		]),
		getAlbumArr() {
			this.$api.ajax({
				url: this.$config.getImgs
			})
			.then((res) => {
				if(res.statusCode == 200 && res.data.code == 0) {
					let arr = res.data.data
					this.albumImgs = arr
				}else {
					this.$api.warnNotice(res.data.message)
				}
			})
			.catch(() => {
				this.$api.warnNotice('相册加载失败')
			})
		},
		swiperAlbumChange(e) {
			this.albumIdx = e.detail.current
		},
		toPreviewAlbum(idx) {
			const $this = this
			uni.previewImage({
				current: $this.albumImgs[idx],
				urls: $this.albumImgs
			})
		},
		toPriviewCert(idx) { //查看证书
			const $this = this
			uni.previewImage({
				current: $this.dataObj.img_list[idx],
				urls: $this.dataObj.img_list
			})
		},
		toEditJoinInit() {
			const $this = this
			if(this.dataObj.uid == this.userInfo.uid) {
				uni.navigateTo({
					url: `/pages/initName/initName?from=edit&id=${$this.dear_id}`
				})
			}
		},
		toJumpDayRoute(idx) { //跳转到某天线路
			if(!this.isShowRouteList) {
				this.isShowRouteList = true
				setTimeout(() => {
					this.viewId = `route_${idx}`
				}, 200)
			}else {
				this.viewId = `route_${idx}`
			}
		},
		curSwiperChange(e) { //swiper下标点
			this.curIdx = e.detail.current
		},
		toShowSmallFee() { //to查看消费
			this.isShowSmallFee = !this.isShowSmallFee
		},
		toCloseSmallFee() { //to关闭消费
			this.isShowSmallFee = !this.isShowSmallFee
		},
		toShowMoreInit() { //查看联合发起人
			if(this.unionArr.length != 0) {
				this.isShowOthInit = true
			}else {
				this.$api.warnNotice('您尚未添加联合发起人')
			}
		},
		toCloseInitBox(e) { //关闭
			let sign = e.target.dataset.sign
			if(sign) {
				this.isShowOthInit = false
			}
		},
		toBackHome() {
			uni.reLaunch({
				url: '/pages/index/index'
			})
		},
		getUserInfo: function(e) { //按钮登录授权
		  if (e.detail.errMsg == 'getUserInfo:ok') {
		    uni.showLoading({
		      title: '授权登陆中...',
		      mask: true
		    })
		    const $this = this
		    this.$api.userLogin((status) => {
		      if (status == '1') {
		         uni.hideLoading()
		         uni.showToast({
		            title: '授权登录成功',
		            icon: 'success',
		            mask: true,
		            success: () => {
						$this.isAuth = true
						$this.userInfo = $this.$userInfo
		            }
		        })
		      }
		    }, this.dear_id)
		  }else {
		    this.$api.warnNotice('您已拒绝授权！')
		    this.isAuth = false
		  }
		},
		toRelaodData() {
			this.isLoadErr = false
			this.dataObj = {}
			this.getData()
		},
		getData() {
			uni.showLoading({
				title: '数据加载中',
				mask: true
			})
			this.$api.ajax({
				url: this.$config.applyActDetail,
				data: {
					no: this.$config.no,
					dear_id: this.dear_id
				}
			})
			.then((res) => {
				if(res.statusCode == 200 && res.data.code == 0) {
					let obj = res.data.data
					obj.tel = obj.tel.substr(0, 3) + '********'
					
					this.dataObj = obj
					this.lineObj = JSON.parse(obj.content)
					this.unionArr = JSON.parse(obj.union_json) || []
					if(this.unionArr.length != 0) {
						this.unionArr.map((v, i) => {
							let stratName = v.union_name.split(' | ')[1]
							if(stratName == '会长' || stratName == '执行会长') {
								v.union_tel = v.union_tel.substr(0, 3) + '********'
							}
						})
					}
					this.lineObj.day_title_list.map(v => {
						v.DTitles = v.title.split(',')
					})
					this.lineObj.day_content_list.map(v => {
						v.content.map(val => {
							val = val.replace(/\<img/gi, '<img style="max-width:100%;height:auto"')
						})
					})
					let arr = [], type = 1
					this.lineObj.point_list.map((v, i) => {
						if(type == 1) {
							arr.push({})
							arr[arr.length-1].firstPort = v
							arr[arr.length-1].morePort = []
							type++
						}else if(type == 2) {
							arr[arr.length-1].morePort.push(v)
							type++
						}else if(type == 3) {
							arr[arr.length-1].morePort.push(v)
							type = 1
						}
					})
					this.swiperArr = arr
					setTimeout(() => {
						if(!this.videoTop) {
							this.queryMultipleNodes('#video', (top) => {
								this.videoTop = top
							})
						}
						uni.hideLoading();
					}, 500)
				}else {
					uni.hideLoading();
					this.$api.warnNotice(res.data.message)
					this.isLoadErr = true
				}
			})
			.catch((err) => {
				uni.hideLoading();
				this.isLoadErr = true
			})
		},
		toShowSharePoster() { //打开海报分享
			this.isShowPorsterBox = true
		},
		toClosePosterBox(val) { //关闭海报分享
			this.isShowPorsterBox = false
		},
		toApplyActivity() { //报名活动
			this.isShowFeeBox = true
		},
		toCloseFeeBox(e) {
			let sign = e.target.dataset.sign
			if(sign) {
				this.isShowFeeBox = false
			}
		},
		toScroll (e) {
			let top = e.detail.scrollTop
			this.navColor = `rgba(255,2555,255,${ top / 400 > 1 ? 1 : top / 400 })`
			this.navTitle = top / 400 > 1 ? 1 : top / 400
			if(top >= 400) {
				this.isShowNavBorder = true
			}else {
				this.isShowNavBorder = false
			}
			
			if(this.videoTop) {
				this.videoPlayCon(top)
			}
		},
		videoPlayCon(val) { //视频播放控制
			let sys = uni.getSystemInfoSync()
			let top = this.videoTop - (sys.windowHeight - sys.windowWidth / 750 * 487)
			if(val - top > 0) {
				this.isPlayVideo = true
			}
		},
		queryMultipleNodes(id,callBack) {
			var query = uni.createSelectorQuery().in(this)//创建节点查询器 query
			query.select(id).boundingClientRect( data => {
				typeof callBack == 'function' && callBack(data.top)
			}).exec()//这段代码的意思是选择Id=the-id的节点，获取节点位置信息的查询请求
		},
		toJumpSpotIntro(info) { //查看景点详情
			this.toSaveSpotInfo(info)
			uni.navigateTo({
				url: '/pages/spotShow/spotShow'
			})
		},
		toShowOptRoute() {
			if(!this.isShowRouteList) {
				this.isShowRouteList = true
				this.optRouteTxt = '收起'
			}else {
				this.isShowRouteList = false
				this.optRouteTxt = '展开'
			}
		},
		toOptApplyList() {
			if(!this.isShowNameList) {
				this.isShowNameList = true
				this.optApplyTxt = '收起'
			}else {
				this.isShowNameList = false
				this.optApplyTxt = '展开'
			}
		},
		toStartCF() { //发起众筹
			const $this = this
			this.toGetActInfo(this.dataObj)
			uni.navigateTo({
				url: `/pages/writeOrder/writeOrder?type=2&dear_id=${$this.dear_id}`
			})
		},
		toJumpApply() {
			const $this = this
			this.toGetActInfo(this.dataObj)
			uni.navigateTo({
				url: `/pages/writeOrder/writeOrder?type=1&dear_id=${$this.dear_id}`
			})
		},
		onShareAppMessage() {
			const $this = this
			return {
				path: `/pages/apply/apply?dear_id=${$this.dear_id}`,
				title: $this.dataObj.title,
				imageUrl: $this.dataObj.banner
			}
		}
	}
}
